:root {
  --header-color: #000000;
  --hover-color: #f5f5f5;
  --active-button: #04aa6d;
  /* ////////////fonts////////// */
  --hover-font-color: #696969;
  --font-color: #d3d3d3;
  --professional-font: "Itim";
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  /* background-image: url(./images/pexels-simone-defendi-12762122.jpg); */
  background-size: cover;
  background-repeat: no-repeat;
}

/* /////////////////////h-tags////////////////////// */
h1 {
  margin: 0;
}

/* /////////////////////NAV/////////////////////// */
.nav {
  border-bottom: 5px solid black;
  border-bottom-right-radius: 100px;
}

.nameTag {
  font-size: 50px;
}

.header {
  overflow: hidden;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px 10px;
}

.header a {
  font-family: var(--professional-font);
  float: left;
  color: black;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  line-height: 25px;
  border-radius: 4px;
}

.header a.logo {
  font-size: 25px;
  font-weight: bold;
}

.header a:hover {
  background-color: #ddd;
  color: black;
  text-shadow: 3px 3px 10px black;
  border-radius: 10px;
}

/* Style the active/current link*/
.header a.active {
  color: white;
}

/* Float the link section to the right */
.header-right {
  float: right;
  font-size: 20px;
  font-weight: bolder;
}

.header-right a:focus {
  background-color: #ddd;
  color: black;
  text-shadow: 3px 3px 10px black;
  border-radius: 10px;
}

.section-heading {
  text-align: center;
  font-family: var(--page-font);
  font-size: 40px;
  letter-spacing: 2px;
}

.information-container {
  display: flex;
  justify-content: space-around;
  font-family: var(--page-font);
}

.information-container img {
  width: 400px;
  border-radius: 20px;
}

.box-content {
  text-align: center;
  width: 60%;
}

.box-content h3 {
  font-size: 30px;
}

.box-content p {
  font-size: 25px;
}

.aboutme-container {
  padding-top: 50px;
  height: 500px;
}

.contact-container {
  text-align: center;
  font-family: var(--page-font);
  font-size: 25px;
}

.contact-container a {
  font-family: var(--page-font);
  font-size: 25px;
}

/* See my work stuff */
#seemywork {
  height: 600px;
}

.showcase {
  text-align: center;
}

.examples-row {
  display: flex;
  justify-content: space-around;
}

.workexample img {
  border-radius: 25px;
}

.showcase a img {
  width: 600px;
}

.examples-row a img {
  width: 300px;
}

@media screen and (max-width: 768px) {
  .nav-btn {
    display: flex;
  }

  .section-heading {
    text-align: center;
    font-family: var(--page-font);
    font-size: 30px;
    letter-spacing: 2px;
  }

  /* aboutme */
  .aboutme-container {
    height: 700px;
    text-align: center;
  }

  .information-container {
    flex-direction: column;
    align-items: center;
  }

  .information-container img {
    width: 200px;
  }

  /* seemywork */
  #seemywork {
    display: flex;
    flex-direction: column;
    height: 700px;
  }

  .showcase a img {
    width: 350px;
  }

  .examples-row {
    flex-direction: column;
    align-items: center;
  }

  .examples-row a img {
    border-radius: 99999px;
    width: 200px;
  }

  .showcase div a h4 {
    text-decoration: none;
    color: pink;
  }
}